class KeyTracker {
  constructor() {
    this._el = document;

    this._addEventListeners();

    this.keyUpCb = {};
    this.keyDownCb = {};
  }

  setEl(el) {
    this._removeEventListeners();
    this._el = el;
    this._addEventListeners();
  }

  _addEventListeners() {
    this._onKeyUp = this._onKeyUp.bind(this);
    this._onKeyDown = this._onKeyDown.bind(this);
    this._el.addEventListener('keyup', this._onKeyUp);
    this._el.addEventListener('keydown', this._onKeyDown);
  }

  _removeEventListeners() {
    this._el.removeEventListener('keyup', this._onKeyUp);
    this._el.removeEventListener('keydown', this._onKeyDown);
  }

  _onKeyUp(e) {
    if (this.keyUpCb[e.key]) {
      this.keyUpCb[e.key].forEach(cb => {
        cb(e);
      })
    }
  }

  _onKeyDown(e) {
    if (this.keyDownCb[e.key]) {
      this.keyDownCb[e.key].forEach(cb => {
        cb(e);
      })
    }
  }

  onKeyUp(keys, cb) {
    let _keys = Array.isArray(keys) ? keys.slice() : [keys];

    for (let i = 0; i < _keys.length; i++) {
      if (!this.keyUpCb[_keys[i]]) {
        this.keyUpCb[_keys[i]] = [];
      }

      this.keyUpCb[_keys[i]].push(cb);
    }
  }

  onKeyDown(keys, cb) {
    let _keys = Array.isArray(keys) ? keys.slice() : [keys];

    for (let i = 0; i < _keys.length; i++) {
      if (!this.keyDownCb[_keys[i]]) {
        this.keyDownCb[_keys[i]] = [];
      }

      this.keyDownCb[_keys[i]].push(cb);
    }
  }
}

export default new KeyTracker();
