import Tile from './Tile';

import { getColor } from '../utils/codevember';
import { colorToString, clamp } from '../utils/utils';
import Messager from './Messager';

class Gamble extends Tile {
  constructor(x,y,size,sprite) {
    super(x,y,size,sprite);

    this.tempLuck = 0;
  }
  get gambleCost() {
    return 5;
  }

  get interactable() {
    return true;
  }

  get label() {
    return ['The bookies', `Gamble $${ this.gambleCost }`];
  }

  update() {
    this.tempLuck -= 0.01;
    this.tempLuck = clamp(this.tempLuck, 1, 0);
  }

  onInteract(player) {
    if (player.hasMoney(this.gambleCost)) {
      player.spend(this.gambleCost);

      if (Math.random() > player.luck + this.tempLuck) {
        const earned = Math.ceil(Math.random() * (this.gambleCost * 5)) + this.gambleCost;
        player.earn(earned);
        Messager.setEphemeral(`$${ earned }`, 60);
        Messager.setEphemeral(`You won!`, 60);
        this.tempLuck += 0.4;
      } else {
        Messager.setEphemeral(`Lose`, 60);
      }
    } else {
      Messager.setEphemeral(`Ya bum!`);
      Messager.setEphemeral(`Can't afford`);
    }
  }
}

export default Gamble;
