import Tile from './Tile';

import { getColor } from '../utils/codevember';
import { colorToString } from '../utils/utils';

class Wall extends Tile {
  get solid() {
    return true;
  }

  drawSprite() {
    ctx.fillStyle = colorToString(getColor('grey'));
    ctx.fillRect(0, 0, this.size, this.size);
  }
}

export default Wall;
