import Tile from './Tile';

import { getColor } from '../utils/codevember';
import { colorToString } from '../utils/utils';
import Messager from './Messager';

class Shop extends Tile {
  get wheatValue() {
    return 5;
  }

  get interactable() {
    return true;
  }

  get label() {
    return ['Sell wheat', `$${ this.wheatValue }`];
  }

  onInteract(player) {
    if (player.wheat > 0) {
      let total = player.wheat;

      player.earn(total * this.wheatValue)
      player.sellWheat(total);

      Messager.setEphemeral(`Sold ${ total } wheat`, 60);
      Messager.setEphemeral(`Earned $${ total * this.wheatValue }`, 60);
    } else {
      Messager.setEphemeral('No wheat to sell');
    }
  }
}

export default Shop;
