import { colorToString } from '../utils/utils';
import { getColor } from '../utils/codevember';

class Messager {
  constructor() {
    this._permanent = '';
    this._ephemeral = [];
    this._internalId = 0;

    this.frame = 0;
    this._total = 2;
  }

  setPermanent(label) {
    this._permanent = label;
  }

  setEphemeral(label, time = 30) {
    const msg = {
      id: this._internalId,
      start: this.frame,
      end: this.frame + time,
      label
    };

    this._internalId++;

    this._ephemeral.unshift(msg);

    this._ephemeral.splice(this._total, 50);
  }

  _trimEphemeral() {
    const temp = this._ephemeral.slice();

    this._ephemeral.forEach(({ id, end }, idx) => {
      if (end <= this.frame) {
        const _idx = temp.findIndex(m => m.id === id);
        temp.splice(_idx, 1);
      }
    });

    this._ephemeral = temp;
  }

  update() {
    this._trimEphemeral();
    this.frame++;
  }

  text(textArr, x, y, size) {
    let arr = Array.isArray(textArr) ? textArr.slice() : [textArr];

    arr.forEach((t, i) => {
      ctx.fillText(t, x, y + (i * size));
    })
  }

  draw(width, tileSize, player) {
    const size = (tileSize / 2);

    ctx.translate(0, size);

    const column = (c, right) => (tileSize * c) + (right ? tileSize : 0);

    ctx.font = `bold ${ tileSize / 3 }px monospace`;

    ctx.fillStyle = colorToString(getColor('grey'));
    this.text(this._permanent, column(0), 0, size);

    ctx.fillStyle = colorToString(getColor('blue'));
    this.text(`$${ player.money }`, column(4), 0, size);

    ctx.fillStyle = colorToString(getColor('blue'));
    this.text([
      `${ player.wheat } wheat`,
      `${ player.bread } bread`
    ], column(5), 0, size);


    ctx.fillStyle = colorToString(getColor('red'));
    ctx.textAlign = 'right';
    this.text(this._ephemeral.map(({ label }) => label), column(9, true), 0, size);
  }
}

export default new Messager();
