import Tile from './Tile';

import { getColor } from '../utils/codevember';
import { colorToString } from '../utils/utils';
import Messager from './Messager';
import Sprite from './Sprite';

const MAKER_STATE = {
  EMPTY: 0,
  BAKING: 1,
  READY: 2,
};

class BreadMaker extends Tile {
  constructor(x, y, size, sprite) {
    super(x, y, size, sprite);

    this.frame = 0;

    this.state = MAKER_STATE.EMPTY;

    this.growTime = 60 * 10;
  }

  get interactable() {
    return true;
  }

  get wheatCost() {
    return 5;
  }

  get tileCost() {
    return 50;
  }

  get label() {
    switch (this.state) {
      case MAKER_STATE.EMPTY:
        return ['Bake bread', `(${ this.wheatCost } wheat)`];
      case MAKER_STATE.BAKING:
        return `Baking ${ Math.round((this.frame / this.growTime) * 100) }%`;
      case MAKER_STATE.READY:
        return `Bread`;
    }
    return
  }

  get purchaseLabel() {
    return `Bread Maker`;
  }

  update() {
    if (this.isBaking() && this.frame > this.growTime) {
      this.setState(MAKER_STATE.READY);
    }

    if (this.isBaking()) {
      this.frame++;
    }
  }

  drawSprite() {
    switch (this.state) {
      case MAKER_STATE.EMPTY:
        Sprite.draw(10, this.size);
        break;
      case MAKER_STATE.BAKING:
        Sprite.draw(11, this.size);
        break;
      case MAKER_STATE.READY:
        Sprite.draw(12, this.size);
        break;
    }

    if (this.isBaking()) {
      ctx.globalAlpha = 0.75;
      let hei = Math.round(this.size / 10);
      let size = this.size / 2;
      let wid = (this.frame / this.growTime) * size;

      ctx.beginPath();
      ctx.fillStyle = colorToString(getColor('grey'));
      ctx.fillRect((this.size - size) / 2, this.size - (hei * 2), size, hei);
      ctx.fillStyle = 'green';
      ctx.fillRect((this.size - size) / 2, this.size - (hei * 2), wid, hei);
    }
  }

  isEmpty() {
    return this.state === MAKER_STATE.EMPTY;
  }

  isBaking() {
    return this.state === MAKER_STATE.BAKING;
  }

  isReadyToHarvest() {
    return this.state === MAKER_STATE.READY;
  }

  setState(state) {
    this.state = state;
  }

  startBaking() {
    this.setState(MAKER_STATE.BAKING);
    this.frame = 0;
  }

  resetBaking() {
    this.setState(MAKER_STATE.EMPTY);
    this.frame = 0;
  }

  onInteract(player) {
    if (this.isEmpty()) {
      if (player.hasWheat(this.wheatCost)) {
        this.startBaking();
        player.sellWheat(this.wheatCost);
        Messager.setEphemeral('Started baking');
      } else {
        Messager.setEphemeral(`Not enough wheat`);
      }
    } else if (this.isBaking()) {
      Messager.setEphemeral(`Not ready yet`);
    } else if (this.isReadyToHarvest()) {
      this.resetBaking();
      player.addBread(1);
      Messager.setEphemeral('Taken 1 bread');
    }
  }
}

export default BreadMaker;
