import Tile from './Tile';

import { getColor } from '../utils/codevember';
import { colorToString } from '../utils/utils';
import Messager from './Messager';
import Ground from './Ground';

const PERK_TYPES = {
  GROW: 0,
  LUCK: 1
};

const PERK_COST = {
  [PERK_TYPES.GROW]: 10,
  [PERK_TYPES.LUCK]: 20,
}

const PERK_NAME = {
  [PERK_TYPES.GROW]: 'Insta-grow!',
  [PERK_TYPES.LUCK]: 'Improve luck',
}

const PERK_DESC = {
  [PERK_TYPES.GROW]: ['Let there', 'be growth!'],
  [PERK_TYPES.LUCK]: ['Luck improved!'],
}

class Perk extends Tile {
  constructor(x, y, size, sprite, perkType = 0) {
    super(x, y, size, sprite);

    this.perkType = perkType;
  }

  get perkCost() {
    return PERK_COST[this.perkType];
  }

  get perkName() {
    return PERK_NAME[this.perkType];
  }

  get interactable() {
    return true;
  }

  get label() {
    return [this.perkName, `$${ this.perkCost }`];
  }

  // drawSprite() {
  //   ctx.fillStyle = 'purple';
  //   ctx.fillRect(0, 0, this.size, this.size);
  // }

  onInteract(player, tiles) {
    if (player.hasMoney(this.perkCost)) {

      if (this.perkType === PERK_TYPES.GROW) {
        tiles.forEach(row => {
          row.forEach(t => {
            if (t instanceof Ground) {
              t.frame = t.growTime;
            }
          });
        });
      }

      if (this.perkType === PERK_TYPES.LUCK) {
        if (player.luck >= 0.5) {
          player.luck -= 0.1;
        } else {
          Messager.setEphemeral('Too lucky!', 60);
          return;
        }
      }

      player.spend(this.perkCost);
      PERK_DESC[this.perkType].map(m => Messager.setEphemeral(m, 60));

    } else {
      Messager.setEphemeral(`Can't afford`);
      Messager.setEphemeral(`$${this.perkCost}`);
    }
  }
}

export default Perk;
