import { loadImage } from '../utils/utils';

const imageFiles = require('./assets/*.png');

class Sprite {
  constructor() {
    this.images = [];
  }

  loadAll() {
    return new Promise((resolve, reject) => {
      const keys = Object.keys(imageFiles);
      const images = [];

      const runner = (index = 0) => {
        if (index >= keys.length) {
          resolve(images);
          return;
        }

        loadImage(imageFiles[keys[index]])
          .then(imgEl => {
            images.push(imgEl);

            runner(index + 1);
          });
      }

      runner(0);
    });
  }

  draw(key, size) {
    const target = parseInt(key) < this.images.length ? parseInt(key) : 0;

    ctx.drawImage(this.images[target], 0, 0, size, size);
  }

  load() {
    return this.loadAll()
      .then(images => {
        this.images = images;
      });
  }
}

export default new Sprite();
