import Tile from './Tile';

import { getColor } from '../utils/codevember';
import { colorToString } from '../utils/utils';
import Messager from './Messager';

class Purchasable extends Tile {
  constructor(x, y, size, sprite, replace) {
    super(x, y, size, sprite);
    this.replace = replace;
  }

  get interactable() {
    return true;
  }

  get label() {
    return [`Purchase`, `${ this.replace.purchaseLabel } ($${ this.replace.tileCost})`];
  }

  // drawSprite() {
  //   const wid = Math.round(this.size / 10);
  //   ctx.lineWidth = wid;
  //   ctx.setLineDash([1, 2]);
  //   ctx.strokeStyle = colorToString(getColor('grey'));
  //   ctx.strokeRect(wid / 2, wid / 2, this.size - wid, this.size - wid);
  // }

  onInteract(player, tiles) {
    if (player.hasMoney(this.replace.tileCost)) {
      player.spend(this.replace.tileCost);
      tiles[this.x][this.y] = this.replace;
      Messager.setEphemeral(`${ this.replace.label[0] }`);
      Messager.setEphemeral(`Purchased`);
    } else {
      Messager.setEphemeral(`($${ this.replace.tileCost })`, 60);
      Messager.setEphemeral(`Can't afford`, 60);
    }
  }
}

export default Purchasable;
