import {colorToString} from './utils';

// Anoying duplication from main file
const WIDTH = 480;
const HEIGHT = 480;

export const TOTAL_DAYS = 30;
export const FRAME_MOD = 3;
export const TOTAL_FRAMES = TOTAL_DAYS * FRAME_MOD;
export const OVERLAY_RADIUS = WIDTH * 0.1;
export const OVERLAY_WIDTH = WIDTH + (OVERLAY_RADIUS * 2);
export const OVERLAY_HEIGHT = HEIGHT * 0.5;
export const PALETTE = {
  red: {
    default: {
      // r: 255,
      // g: 102,
      // b: 94
      r: 255,
      g: 116,
      b: 119,
      hex: 0xFF7477
    }
  },
  blue: {
    default: {
      // r: 0,
      // g: 120,
      // b: 191
      r: 98,
      g: 168,
      b: 229,
      hex: 0x62A8E5
    }
  },
  grey: {
    default: {
      r: 184,
      g: 199,
      b: 196,
      hex: 0xB8C7C4
    }
  }
};


/**
 * Draws the overlay in its most default fashion
 *
 * @param {number} x
 * @param {number} y
 * @param {number} radius
 */
export const defaultDrawOverlay = (x, y, radius) => {
  ctx.save();
  ctx.fillStyle = 'white';
  ctx.lineWidth = 4;
  ctx.strokeStyle = colorToString(getColor('red'));
  ctx.beginPath();
  ctx.arc(x, y, radius - 4, 0, Math.PI * 2);
  ctx.stroke();
  // ctx.fill();
  ctx.restore();
};


/**
 * Works out the position of the frame necessary
 *
 * @param {number} day Day of the month
 * @param {number} width Width of the animation area
 * @param {number} height Height of the animation area
 * @param {function?} drawOverlay Function that will draw the overlay on top
 */
export const createOverlay = (day, width, height, drawOverlay = defaultDrawOverlay) => {
  const frame = (day - 1) * FRAME_MOD;
  const perc = (frame % TOTAL_FRAMES) / TOTAL_FRAMES;
  const x = (perc * OVERLAY_WIDTH) - OVERLAY_RADIUS;
  const y = (Math.sin(perc * (Math.PI * 2)) * (OVERLAY_HEIGHT / 2)) + height / 2;

  drawOverlay(x, y, OVERLAY_RADIUS);
};


/**
 * Returns a colour matching the key and level
 * from the palette
 *
 * @param {string} key
 * @param {string?} level
 */
export const getColor = (key, level = 'default') => {
  return PALETTE[key][level];
};


/**
 * A method to draw the palette as squares
 *
 * @param {number?} x
 * @param {number?} y
 * @param {number?} size
 */
export const drawPalette = (x = 0, y = 0, size = 10) => {
  ctx.save();
  ctx.translate(x, y);
  Object.keys(PALETTE).forEach((v, index) => {
    ctx.save();
    ctx.translate(index * size, 0);
    ctx.fillStyle = colorToString(getColor(v));
    ctx.fillRect(0, 0, size, size);
    ctx.restore();
  });
  ctx.restore();
}
