import Tile from './Tile';

import { getColor } from '../utils/codevember';
import { colorToString, color } from '../utils/utils';
import Messager from './Messager';
import Sprite from './Sprite';

const PLANT_STATE = {
  EMPTY: 0,
  GROWING: 1,
  READY: 2,
};

class Ground extends Tile {
  constructor(x, y, size, sprite) {
    super(x, y, size, sprite);

    this.frame = 0;

    this.state = PLANT_STATE.EMPTY;

    this.growTime = 60 * 8;
  }

  get interactable() {
    return true;
  }

  get cost() {
    return 3;
  }

  get tileCost() {
    return 10;
  }

  get label() {
    switch (this.state) {
      case PLANT_STATE.EMPTY:
        return ['Fertile soil', `($${ this.cost })`];
      case PLANT_STATE.GROWING:
        return `Growing crops ${ Math.round((this.frame / this.growTime) * 100) }%`;
      case PLANT_STATE.READY:
        return `Wheat`;
    }
  }

  get purchaseLabel() {
    return `Soil`;
  }

  update() {
    if (this.isGrowing() && this.frame > this.growTime) {
      this.setState(PLANT_STATE.READY);
    }

    if (this.isGrowing()) {
      this.frame++;
    }
  }

  drawSprite() {
    switch (this.state) {
      case PLANT_STATE.EMPTY:
        Sprite.draw(7, this.size);
        break;
      case PLANT_STATE.GROWING:
        Sprite.draw(8, this.size);
        break;
      case PLANT_STATE.READY:
        Sprite.draw(9, this.size);
        break;
    }

    if (this.isGrowing()) {
      ctx.globalAlpha = 0.75;
      let hei = Math.round(this.size / 10);
      let size = this.size / 2;
      let wid = (this.frame / this.growTime) * size;

      ctx.beginPath();
      ctx.fillStyle = colorToString(getColor('grey'));
      ctx.fillRect((this.size - size) / 2, this.size - (hei * 2), size, hei);
      ctx.fillStyle = 'green';
      ctx.fillRect((this.size - size) / 2, this.size - (hei * 2), wid, hei);
    }
  }

  isEmpty() {
    return this.state === PLANT_STATE.EMPTY;
  }

  isGrowing() {
    return this.state === PLANT_STATE.GROWING;
  }

  isReadyToHarvest() {
    return this.state === PLANT_STATE.READY;
  }

  setState(state) {
    this.state = state;
  }

  startGrowing() {
    this.setState(PLANT_STATE.GROWING);
    this.frame = 0;
  }

  resetGrowing() {
    this.setState(PLANT_STATE.EMPTY);
    this.frame = 0;
  }

  onInteract(player) {
    if (this.isEmpty()) {
      if (player.hasMoney(this.cost)) {
        this.startGrowing();
        player.spend(this.cost);
        Messager.setEphemeral('Planted crops');
      } else {
        Messager.setEphemeral(`Can't afford ($${ this.cost })`);
      }
    } else if (this.isGrowing()) {
      Messager.setEphemeral(`Can't harvest yet`);
    } else if (this.isReadyToHarvest()) {
      this.resetGrowing();
      player.addWheat(1);
      Messager.setEphemeral('Taken 1 wheat');
    }
  }
}

export default Ground;
