import Tile from './Tile';

import Vector from '../utils/vector';
import { getColor } from '../utils/codevember';
import { colorToString } from '../utils/utils';
import Messager from './Messager';

class Player extends Tile {
  constructor(x, y, size, sprite) {
    super(x, y, size, sprite);

    this.speed = new Vector({ x: 0, y: 0 });

    this.money = 15;
    this.wheat = 0;
    this.bread = 0;
    this.luck = 0.9;
  }

  update(tiles) {
    const position = this._position.clone().add(this.speed);

    const resetX = () => position.x = this.x;
    const resetY = () => position.y = this.y;

    if (position.x < 0 || position.x >= tiles.length) {
      resetX();
    }
    if (position.y < 0 || position.y >= tiles[0].length) {
      resetY();
    }

    const nextTile = tiles[position.x][position.y];

    if (nextTile.solid) {
      resetX();
      resetY();
    }

    Messager.setPermanent(nextTile.label);

    this._position.set(position.x, position.y);

    this.speed.x = 0;
    this.speed.y = 0;
  }

  drawSprite() {
    const col = colorToString(getColor('grey'));
    const width = Math.round(this.size / 10);
    ctx.lineWidth = width;
    ctx.strokeStyle = col;
    ctx.fillStyle = col;

    ctx.beginPath();
    ctx.arc(this.size / 2, this.size / 2, (this.size - width) / 2, 0, Math.PI * 2);
    ctx.stroke();
    // ctx.strokeRect(width / 2, width / 2, this.size - width, this.size - width);
    ctx.globalAlpha = 0.5;
    ctx.fill();
    // ctx.fillRect(width / 2, width / 2, this.size - width, this.size - width);
  }

  up() {
    this.speed.y = -1;
  }

  down() {
    this.speed.y = 1;
  }

  left() {
    this.speed.x = -1;
  }

  right() {
    this.speed.x = 1;
  }

  interact(tiles) {
    const tile = tiles[this.x][this.y];

    if (tile.interactable) {
      tile.onInteract(this, tiles);
    }
  }

  hasMoney(money) {
    return this.money >= money;
  }

  spend(money) {
    this.money -= money;
  }

  earn(money) {
    this.money += money;
  }

  hasWheat(wheat) {
    return this.wheat >= wheat;
  }

  addWheat(wheat) {
    this.wheat += wheat;
  }

  sellWheat(wheat) {
    this.wheat -= wheat;
  }

  addBread(bread) {
    this.bread += bread;
  }
}

export default Player;
